import React from "react";
import "./App.css";
import AboutMe from "./Components/aboutme";
import Navbar from "./Components/navbar";
import Reveal, { RevealRight, RevealOnly } from "./Components/Reveal";
import styled from "styled-components";
import Coverpage from "./Components/Coverpage";

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      highest: 0,
      position: "relative",
      padding: "-20px",
      increase_num: 1,
      set_opacity: 0
    };
  }

  componentDidMount() {
    window.addEventListener("scroll", this.set_position_header, false);
  }

  componentWillUnmount = () => {
    window.removeEventListener("scroll", this.set_position_header);
  };

  // Sets the position of the nav bar. Scrolling down = false; Scrolling up = true.
  set_position_header = () => {
    let offsetY = window.pageYOffset;

    if (offsetY <= 40) {
      this.setState({
        position: "relative",
        padding: "-20px",
        increase_num: 1
      });
    } else if (
      offsetY < this.state.highest &&
      this.state.increase_num >= 0 &&
      this.state.increase_num <= 20
    ) {
      this.setState({
        increase_num: 1 + this.state.increase_num
      });
    } else if (offsetY < this.state.highest && this.state.increase_num >= 21) {
      this.setState({
        highest: offsetY,
        position: "fixed",
        padding: "-20px",
        increase_num: 0
      });
    } else {
      this.setState({
        highest: offsetY,
        position: "relative",
        increase_num: 0
      });
    }
  };

  set_state = () => {
    this.setState({ show: true });
  };
  render() {
    const hold = styled.div`<header className="App-header">
    <RevealOnly>
      <Navbar links={this.state}></Navbar>
    </RevealOnly>
  </header>
  <RevealRight>hi</RevealRight>
  <Reveal>
    <section>
      <AboutMe></AboutMe>
    </section>
  </Reveal>`;
    return (
      <div className="App">
        <Coverpage></Coverpage>
      </div>
    );
  }
}

export default App;
